// section typealias
type GoogleMaps = google.maps.Map

export class CustomMap {
    //: - ©Member-PROPERTIES
    // #™━━━━━━━━━━━━━━━━━━━━━━━━
    private googleMap: GoogleMaps
    // #™━━━━━━━━━━━━━━━━━━━━━━━━

    /** ™- Constructor */
    constructor(divID: string) {
        //..........
        this.googleMap = new google.maps.Map(
            document.getElementById(divID), {
                //..........
                zoom: 1,
                center: {
                    //..........
                    lat: 0,
                    lng: 0
                }
            })
    }
    /// - END OF: constructor
}
/** - END OF: @CustomMap */
